.dataTable{
    max-width: 100%;

    .dataGrid{
        max-width: 100%;
        background-color: white;
        padding: 20px;

        .MuiDataGrid-toolbarContainer{
            max-width: 100%;
            flex-direction: row-reverse;
        }

        img{
            width: 32px;
            height: 32px;
            border-radius: 50%;
            object-fit: cover;
        }

        .action{
            display: flex;
            gap: 15px;

            img{
                width: 20px;
                height: 20px;
                cursor: pointer;
            }
        }
    }
}
